import React from "react";
import { Router } from "@reach/router";

import PrivateRoute from "../components/PrivateRoute";
import { navigate } from "gatsby";

export default () => {
  return (
    <Router basepath="/private">
      <PrivateRoute path="/*" name="do-you-bake" />
      <Default path="/" />
    </Router>
  );
};

function Default() {
  navigate("/");
  return null;
}
